import React from 'react';
import './Usercard.css'
import Popup from "reactjs-popup";

const review =[{
  stars:5,
  name: "Kiss Kata",
  review: "Nagyon j= a vweboldadl ug tovabb es kitartast"
},{
  stars:5,
  name: "Kiss Kata",
  review: "Nagyon j= a vweboldadl ug tovabb es kitartast"
}]
const solveStarts = (amount) =>{
  
    let draw = "";
    for (let index = 0; index < amount; index++) {
     draw += "⭐";
    }
    return draw;
  
}
const reviewCard = (reviews) =>{
  if(reviews[0] != null){
  return reviews.map((reviewD,i)=>{
    return(
      <div className="reviewcard">
        <h6>{reviewD.name} {solveStarts(parseInt(reviewD.star))}
        
        </h6>
        <p>{reviewD.review}</p>
      </div>
    )
  })}else{
    return <h6>Még nem érkezett róla vélemény legyél te az első!</h6>

  }
}
const statusCard = (status)=> {
    switch (status) {
        case 0:
         return(
             <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "grey",borderRadius:"100vw"}}>
             <h4 className="u-text u-text-default u-text-2 whiten">Nem elérhető</h4>
               </div>
         )
            break;
            case 1:
             return(
                 <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "green",borderRadius:"100vw"}}>
                 <h4 className="u-text u-text-default u-text-2 whiten">Elérhető</h4>
                   </div>
             )
            break;
            case 2:
             return(
                 <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "red",borderRadius:"100vw"}}>
                 <h4 className="u-text u-text-default u-text-2 whiten">Foglalt</h4>
                   </div>
             )
            break;
        default:
         return(
             <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "grey",borderRadius:"100vw"}}>
             <h4 className="u-text u-text-default u-text-2 whiten">Nem elérhető</h4>
               </div>
         )
            break;
    }
 }
 const solvePrice = (baseprice,multiplyer,timeframe) =>{
   return(<div>
    <h6>{timeframe} Perc - <s>{(baseprice*(timeframe/10)).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")} HUF</s> {(multiplyer).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")} HUF</h6>
    </div>
  )
 }
class Usercard extends React.Component {

    constructor(props) { 
        super(props);
        this.state = props;
       
      }
    componentDidUpdate(prevProps) {
        if (prevProps !== this.props) {
          this.setState(this.props);
        }
      } 
    
    render() {
        
            return(
              <div>
                <div classNameName="u-container-layout u-valign-top u-container-layout-1" style={{display:'flex',margin:"15px",justifyContent:'center',maxWidth: 'max-content'}}>
                    <div className="u-align-center-xs u-container-style u-expanded-width-xs u-group u-group-1 containercard ">
                      <div className="u-container-layout u-valign-bottom-xs u-container-layout-2 expandebled" style={{padding:'0px',height: "max-content"}}>
                        <div className="u-container-style u-expanded-width u-group u-image u-image-1" data-image-width="1280" data-image-height="941" style={{backgroundImage: `url("${this.state.udata.avatar}")`}}>
                          <div className="u-container-layout u-container-layout-3">
                            <div className="u-align-center u-container-style u-custom-color-3 u-group u-shape-rectangle u-group-3" style={{borderRadius:"100vw"}}>
                            {statusCard(this.state.udata.status)}
                             
                            </div>
                          </div>
                        </div>
                       <div className="basicinfo">
                       <h3 className="name">{this.state.udata.name}</h3>
                        <blockquote className="" style={{width: "100%",alignSelf: "flex-end !important",textAlign: "center"}}>"{this.state.udata.quote}"</blockquote>
                        <h4 className="rating">Értékelés: ⭐⭐⭐⭐⭐</h4>
                        <Popup className="ppup" trigger={<button className="btn"> Jóslást szeretnék </button>} modal>
                          {close => (
                            <div className="modal">
                            
                              <div className="modaltitle"> <h3>{this.state.udata.name}  ⭐⭐⭐⭐⭐</h3> <button
                                  className="button"
                                  onClick={() => {
                                    close();
                                  }}
                                >
                                  Vissza
                                </button></div>
                                <div className="userdesc">
                                   <div className="scroll">
                                    <div className="viewbob">
                                    <img alt="userpicture" className="upicture" src={this.state.udata.avatar}/>
                                  <p>{this.state.udata.desciptrion}</p>
                                  </div>
                                  <div  className="viewbob">
                                  <h4>Jóslás Árak</h4>
                                  <hr/>
                                {solvePrice(4850,3990,10)}
                                {solvePrice(4850,7990,20)}
                                {solvePrice(4850,12990,30)}
                                {solvePrice(4850,15990,40)}
                                {solvePrice(4850,21990,60)}
                                  <div className="helper">
                                <p><strong>Az utalás megjegyzésébe írja be: telefonszámát,<br/> számlázási nevét ,<br/> számlázási címét</strong></p>
                                <p>{this.state.udata.bank}</p>
                               <p><strong>Sikeres utalás után mi fogjuk önt hívni!</strong></p>
                                  </div>
                                  <h4>Vélemények</h4>
                                  <hr/>
                                  {reviewCard(this.state.udata.reviews)}
                               </div>
                               </div>
                                </div>
                             
                            </div>
                          )}
                        </Popup>
                       </div>
                      </div>  
                    </div>
                  </div>
                  </div>
            )
        
    
    }
  }
export default Usercard;

