socket.on('logincheck', function(data){
    component.setState({user:data})
    component.setState({loggedIn:data.loggedIn});
    component.setState({attempt:1});
    if(data.loggedIn == true && component.state.admin == true){
        axios.post("https://192.168.1.69:8080/adminow",{
            adminpass:component.state.password,
            sockedid: socket.id
        });
    }
});    
socket.on('modifyanswer',function(data){
    component.setState({response:data});
    if(data.status == 0){
        axios.post("https://192.168.1.69:8080/adminow",{
            adminpass:component.state.password,
            sockedid: socket.id
        })   ;
    }

});
socket.on('adminow',function(data){
   
    component.setState({users:data});
    let sumMinutesLocal = 0;
    component.state.users.forEach(element => {
        sumMinutesLocal += parseInt(element.minutes);
        
    });
    component.setState({sumMinutes:sumMinutesLocal})
})
   

