class LoginButton extends React.Component {
    constructor(props){
        super(props);
        this.state = props;
    }
    componentWillReceiveProps(nextProps) {
        this.setState(nextProps);
        
    }
    redirect=()=>{

    }
    render(){
      
        if(this.state.attempt > 0 ){
           return( <div className="wrongholder">
                <p className="wrongLogin">Felhasználói kód vagy a jelszó helytelen</p>
                <button onClick={this.state.process}>Bejelentkezés</button>   
            </div>)
       }
       else{
        return <button onClick={this.state.process}>Bejelentkezés</button>   
       }
    }
      
}

class Login extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
            userid: '',
            password: '',
            admin: false,
            loggedIn : false,
            attempt: 0,
            response: {
                status :4,
                message : "testponse"
            },
            users:'',
            sumMinutes: 0,
        }
        component = this;
       
        
       
        
    }
             processlogin =  () =>{
                axios.post("https://192.168.1.69:8080/loginverify",{ 
                    userid : parseInt(this.state.userid),
                    password: this.state.password,
                    admin: this.state.admin,
                    socketid: socket.id
                });
                 
            }
    onInputChange = (event) => {
      
               switch (event.target.id) {
                   case 'userid':
                        this.setState({userid : event.target.value})
                       break;
                       case 'password':
                        this.setState({password : event.target.value})
                        break;
                        case 'admin':
                           
                            this.setState({admin : !(this.state.admin)})
                            break;
                   default:
                       break;
               }
    }
    processStatusChage=(event)=>{
        axios.post("https://192.168.1.69:8080/statuschange",{
                        
            code : parseInt(this.state.userid),
                    status : parseInt(event.target.id),
                    hash : this.state.user.hash
                });
        this.state.user.status = parseInt(event.target.id);
       this.setState(this.state);
    }
    SetButtonFrameAdmin =()=>{
        switch (this.state.user.status) {
            case 0:
                
                return(
                    <div className="Adminstatus">
                         <button id="1" onClick={this.processStatusChage} className="onlineButton">Elérhető</button>
                        <button id="2" onClick={this.processStatusChage} className="busyButton">Vonalban</button>
                        <button id="0" onClick={this.processStatusChage} className="offlineButton activeButton">Nem Elérhető</button>
                    </div>
                    
                    )
                
                break;
                case 1:
                    
                    return(
                        <div className="Adminstatus">
                             <button id="1" onClick={this.processStatusChage} className="onlineButton activeButton">Elérhető</button>
                            <button id="2"  onClick={this.processStatusChage}className="busyButton">Vonalban</button>
                            <button id="0"  onClick={this.processStatusChage}className="offlineButton">Nem Elérhető</button>
                        </div>
                    )
                    break;
                    case 2:
                    
                        return(
                            <div className="Adminstatus">
                                 <button id="1"  onClick={this.processStatusChage}className="onlineButton">Elérhető</button>
                                <button id="2" onClick={this.processStatusChage} className="busyButton activeButton">Vonalban</button>
                                <button id="0"  onClick={this.processStatusChage}className="offlineButton">Nem Elérhető</button>
                            </div>
                        )
                        break;
        
            default:
                return(
                    <div style={{height: "100%"}}>
                       <p>Status error try reloading page or contact administrator</p>
                    </div>
                )
                break;
        }
    }
    SetButtonFrame =()=>{
        switch (this.state.user.status) {
            case 0:
                
                return(
                    <div style={{height: "100%"}}>
                         <button id="1" onClick={this.processStatusChage} className="onlineButton">Elérhető</button>
                        <button id="2" onClick={this.processStatusChage} className="busyButton">Vonalban</button>
                        <button id="0" onClick={this.processStatusChage} className="offlineButton activeButton">Nem Elérhető</button>
                    </div>
                    
                    )
                
                break;
                case 1:
                    
                    return(
                        <div style={{height: "100%"}}>
                             <button id="1" onClick={this.processStatusChage} className="onlineButton activeButton">Elérhető</button>
                            <button id="2"  onClick={this.processStatusChage}className="busyButton">Vonalban</button>
                            <button id="0"  onClick={this.processStatusChage}className="offlineButton">Nem Elérhető</button>
                        </div>
                    )
                    break;
                    case 2:
                    
                        return(
                            <div style={{height: "100%"}}>
                                 <button id="1"  onClick={this.processStatusChage}className="onlineButton">Elérhető</button>
                                <button id="2" onClick={this.processStatusChage} className="busyButton activeButton">Vonalban</button>
                                <button id="0"  onClick={this.processStatusChage}className="offlineButton">Nem Elérhető</button>
                            </div>
                        )
                        break;
        
            default:
                return(
                    <div style={{height: "100%"}}>
                       <p>Status error try reloading page or contact administrator</p>
                    </div>
                )
                break;
        }
    }
    UserDisplaySolver= ()=>{
      
        if(this.state.users != ''){
            return this.state.users.map((user,i) =>{
                return   <AdminuserOW pass={this.state.password} user={user}/>
            })

        }else{
            return <h1>Töltés...</h1>
        }
       
    }
    PerformanceSolver =()=>{
       
        
        
        return(
                <div  className="sumprop">
                    <div  className="sumpropElment">
                    <h5>Összbevétel</h5>
                    <h5 className="cash">{Math.round(this.state.sumMinutes*449,2)} HUF</h5>
                    </div>
                    <div className="sumpropElment">
                    <h5>Összfizetés</h5>
                    <h5 className="cash">{Math.round(this.state.sumMinutes*449,2)} HUF</h5>
                    </div>
                </div>
        )
    }
    ServerResponsve = () => {
        
        switch (parseInt(this.state.response.status)) {
            case -1:
                return (
                    
                    <div style={{padding : "10px",width : "100vw",backgroundColor : "red"}}><p style={{color: "white"}}>{this.state.response.message}</p></div>
                    )
                    
            break;
            case 0:
                return (
                    
                    <div style={{padding : "10px",width : "100vw",backgroundColor : "green"}}><p style={{color: "white"}}>{this.state.response.message}</p></div>
                    )
            break;
            case 1:
                return (
                    
                    <div style={{padding : "10px",width : "100vw",backgroundColor : "yellow"}}><p>{this.state.response.message}</p></div>
                    )
            break;
            default:
                return(<div></div>)
                break;
        }
    }
    render(){

       
            if(this.state.loggedIn == false){
                return(
                    <div className="logindiv">
                    <nav className="userNav" style={{justifyContent : "center"}}>
                    <h1>Angyal Vonal Alkalmazott</h1>
                    </nav>
                    <div class="formholder">
               
                    <h1 className="logintitle">Bejelentkezés</h1>
                    <input onChange={this.onInputChange} type="text" id="userid" name="userid" placeholder="Felhasználó kód"></input>
                    <input onChange={this.onInputChange} type="password" id="password" name="password" placeholder="Jelszó"></input>
                    <div className="adminbutton"><input onChange={this.onInputChange} type="checkbox" id="admin" name="admin"></input><p>Admin</p></div>
                    <LoginButton attempt={this.state.attempt} store={this.state.loggedIn} process={this.processlogin}/>
                   
                    
            </div>
                       </div>
                )
            }else if(this.state.admin == true)
            {
               
                return(
                    <div>
                    <nav className="userNav adminav">
                    <h1>Angyal Vonal admin</h1>
                    <this.SetButtonFrameAdmin/>
                    <this.PerformanceSolver/>
                </nav>
                    <div className="adminuseroverview" style={{backgroundImage : `url("https://192.168.1.69:8080/public/static/media/sky.png")`,backgroundSize : "cover"}}>
                        <h2 style={{color : "white "}}>Felhasználók</h2>
                        <div className="adminoverviewScroll">
                        <this.UserDisplaySolver/>
                        </div>
                    </div>
                    <div className="adminusermanage">
                        <this.ServerResponsve/>
                        <AdminuserMG password={this.state.password}/>
                    </div>
                    </div>
                
                
                )
            }else{
              
                    if(this.state.user != null){return(
                    <div>
                    <nav className="userNav alkalmazott">
                        <h1>Helló {this.state.user.name.split(' ')[1]} jó újra itt látni</h1>
                    </nav>
                    <div className="userContentHolder">
                    
                            <div className="uchLeft">
                                    <h1>Üzenetek</h1>
                                
                                        <Message messages={this.state.user.messages}/>
                                    
                            </div>
                            <div className="uchRight">
                                <div className="uchRightTop">
                                <h1 className="stateTitle">Állapotkezelés</h1>
                                <this.SetButtonFrame/>
                                            
                                <h6>Eddig ledolgozott percek száma: {this.state.user.minutes} perc</h6>
                                </div>

                                <div className="uchRightBottom">
                                <p>A kezelőfelület nem megfelelő használata következményeket von maga után</p>
                                </div>
                            </div>
                        
                    </div>
                    </div>)}
                    else{
                        return <h1>Töltés..</h1>
                    }
                
            }
        
   
}}

export default Login;