import React from 'react';
import './AdminuserOW.css'
import axios from 'axios';
import socket from '../../helpers/sockets';

class AdminuserOW extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            adminpass: this.props.pass,
            message:'',
            title:'',
            user : this.props.user,
            dropped : 'none'
        };
    }
    dropMessagedialoge=()=>{
        
        if (this.state.dropped == 'none') {
            this.setState({dropped: 'flex'})
        } else {
            this.setState({dropped: 'none'})
        }
    }
    componentDidUpdate(prevprops){
        if(this.props != prevprops){
            this.setState({user : this.props.user});
        }
    }
    processMessage = () =>{
        this.dropMessagedialoge()
    }
    SendMessage = () =>{
        axios.post('https://192.168.1.69:8080/message',{
            code:this.state.user.code,
            title: this.state.title,
            message: this.state.message,
            socketid: socket.id,
            adminpass: this.state.adminpass
            })
    }
    processMessageChange = (event) => {
        switch(event.target.id){
            case "title":
                this.setState({title: event.target.value});
                break;
                case "message":
                    this.setState({message: event.target.value});
                break;
            default:

                    break;
        }
    }
    SovleStatus = () =>{
        switch (this.state.user.status) {
            case 0:
                return <p style={{color : "grey"}}>Állapot: Nem elérhető</p>
                break;
                case 1:
                    return <p style={{color : "green"}} >Állapot: Elérhető</p>
                    break;
                    case 2:
                        return  <p style={{color : "red"}}>Állapot: Vonalban</p>
                break;
            default:
                return<p>Ismeretlen Állapot</p>
                break;
        }
    }
    render(){
        if (!this.state.user.admin) {
            return(
                <div className="user">
                   <div className="usermain"> 
                    <h5>{ this.state.user.name}</h5>
                    <div className="separator"/>
                    <p>Ledolgozott: { this.state.user.minutes} Perc</p>
                    <div className="separator"/>
                    <div className="money"><p>Bevétel </p><p className="huf"> {Math.round( this.state.user.minutes*449,2)} HUF</p></div>
                    <div className="separator"/>
                    <div className="money"><p>Fizetés </p><p className="huf"> {Math.round( this.state.user.minutes*449/3,2)} HUF</p></div>
                    <div className="separator"/>
                    <p>Kód: { this.state.user.code}</p>
                    <div className="separator"/>
                    <this.SovleStatus/>
                    <button  onClick={this.dropMessagedialoge} className="messageButton"><img src="https://192.168.1.69:8080/public/static/media/mail.png" className="messagButtonIamge"></img></button>
                    </div>
                    <div className="messagedialog" style={{display:this.state.dropped}}>
                        <input onChange={this.processMessageChange} type="text" name="title" id="title" placeholder="Cím"></input>
                        <input onChange={this.processMessageChange} type="textfield" name="message" id="message" placeholder="Üzenet"></input>
                        <button onClick={this.SendMessage}>Elküld</button>
                    </div>
                </div>
            )
        }else
        {
            return(
                <div className="user">
                   <div className="usermain"> 
                    <h5>{ this.state.user.name}</h5>
                    <div className="separator"/>
                    <p>Ledolgozott: { this.state.user.minutes} Perc</p>
                    <div className="separator"/>
                    <div className="money"><p>Bevétel </p><p className="huf"> {Math.round( this.state.user.minutes*449,2)} HUF</p></div>
                    <div className="separator"/>
                    <div className="money"><p>Fizetés </p><p className="huf"> {Math.round( this.state.user.minutes*449/3,2)} HUF</p></div>
                    <div className="separator"/>
                    <p>Kód: { this.state.user.code}</p>
                    <div className="separator"/>
                    <this.SovleStatus/>
                    </div>
                </div>
            )}
        }
   
}
export default AdminuserOW;