import React from 'react';
import ReactDOM from 'react';
import './AdminuserMG.css'
import axios from 'axios';
import socket from '../../helpers/sockets';



  class AdminuserMG extends React.Component{
    constructor(props){
        super(props);
        this.clearInputs = this.clearInputs.bind(this)
        this.state={
            option: 'new',
            password: this.props.password,
            toDeleteID:0,
            toDeleteName:'',
            toCreate : {
                name : '',
                code: 0,
                quote: '',
                minutes:'',
                image:null,
                fileext:'',
                socketid: socket.id,
                stars: 5
            },
            toclearArray:[]
        };
    }
    
    clearInputs = () =>{
        if(this.state.toclearArray != []){
            this.state.toclearArray.forEach((input,i)=>{
               input.value ='';
            })
            this.forceUpdate();
            this.state.clearInputs = [];
        }
    }
    deleteuser = () => {
        axios.post("https://192.168.1.69:8080/modify",{ 
                    socketid: socket.id,
                    toDeleteID : parseInt(this.state.toDeleteID),
                    adminpass: this.state.password,
                    mode : "delete"
                });
                this.clearInputs();
    }
    ChangeValueInState = (event) =>{
        this.state.toclearArray.push(event.target);
       switch (event.target.id) {
           case "toDeleteID":
            this.setState({toDeleteID : event.target.value});
               break;
            case "createUserName":
                this.state.toCreate.name = event.target.value;
                this.setState(this.state);
                break;
            case "createUserQuoute":
                this.state.toCreate.quote = event.target.value;
                this.setState(this.state);
                break;
            case "createUserMinutes":
                this.state.toCreate.minutes = event.target.value;
                this.setState(this.state);
                break;
            case "createUserCode":
                this.state.toCreate.code = event.target.value;
                this.setState(this.state);
                break;
            case "createUserPicture":
                this.state.toCreate.image = event.target.files[0];
                this.state.toCreate.fileext = "." + ( event.target.files[0].type.split("/")[1]);
                this.setState(this.state);
                break;
            case"createUserPrice":
            this.state.toCreate.price = event.target.value;
            this.setState(this.state);
            break;
            case"createUserDescription":
            this.state.toCreate.description = event.target.value;
            this.setState(this.state);
            break;
            case"createUserBank":
            this.state.toCreate.bank = event.target.value;
            this.setState(this.state);
            break;
            case"toDeleteName":
            this.state.toDeleteName = event.target.value;
            this.setState(this.state);
            break;
            case"toCreateStarts":
            this.state.toCreate.stars = event.target.value;
            this.setState(this.state);
            break;
           default:
               break;
       }
    }
    CreateUser = () =>{
        const url = 'https://192.168.1.69:8080/upload';
                const formData = new FormData();
                formData.append('avatar',this.state.toCreate.image);
                formData.append('code',this.state.toCreate.code);
                formData.append('name',this.state.toCreate.name);
                formData.append('quote',this.state.toCreate.quote);
                formData.append('minutes',this.state.toCreate.minutes);
                formData.append('fileext',this.state.toCreate.fileext);
                formData.append('bank',this.state.toCreate.bank);
                formData.append('price',this.state.toCreate.price);
                formData.append('desciptrion',this.state.toCreate.description);

                formData.append('socketid',socket.id);

                const config = {
                    headers: {
                        'content-type': 'multipart/form-data'
                    }
                }
                axios.post(url, formData,config)
    }
    deletereview = () =>{
        axios.post("https://192.168.1.69:8080/review",{ 
            socketid: socket.id,
            toDeleteID : this.state.toDeleteID,
            adminpass: this.state.password,
            mode : "delete",
            name: this.state.toDeleteName

        });
    }
    addreview = () =>{
        axios.post("https://192.168.1.69:8080/review",{ 
            socketid: socket.id,
            code : this.state.toCreate.code,
            adminpass: this.state.password,
            mode : "new",
            name: this.state.toCreate.name,
            review: this.state.toCreate.description,
            stars: this.state.toCreate.stars


        });
    }
    ResolveState = () =>{
        switch (this.state.option) {
            case 'new':
               return (
                <div className="createform">
                <h6>Felhasználó létrehozás</h6>
                
                    <form></form>
                <input  onChange={this.ChangeValueInState} type="text" id="createUserName" name="createUserName" placeholder="Felhasználó neve"/>
                <input onChange={this.ChangeValueInState} type="text" id="createUserQuoute" name="createUserQuoute" placeholder="Felhasználó idézete"/>

                <input onChange={this.ChangeValueInState} type="text" id="createUserDescription" name="createUserDescription" placeholder="bemutatkozás"/>
                <input onChange={this.ChangeValueInState} type="text" id="createUserBank" name="createUserBank" placeholder="bankszámlaszám"/>


                <input onChange={this.ChangeValueInState} type="number" id="createUserCode" name="createUserCode" placeholder="kód"/>
                <div className="profliePictureDiv"><p>Profilkép</p>
                <input onChange={this.ChangeValueInState} type="file" id="createUserPicture" name="createUserPicture" placeholder="Profilkép"/></div>
                
                <button onClick={this.CreateUser}>létrehozás</button>
                 </div>)
                
                case 'delete':
                    return   (
                        <div className="deleteform">
                            <h6>Felhasználó törlés</h6>
                            <input onChange={this.ChangeValueInState} type="number" id="toDeleteID" name="toDeleteID" placeholder="kód"/>
                            <button onClick={this.deleteuser}>Törlés</button>
                        </div>
                        
                        )
                   case 'reviewdelete':
                       return( <div className="deleteform"><h6>Vélemények törlés</h6>
                         <input onChange={this.ChangeValueInState} type="number" id="toDeleteID" name="toDeleteID2" placeholder="kód"/>
                         <input onChange={this.ChangeValueInState} type="text" id="toDeleteName" name="toDeleteName2" placeholder="név"/>
                            <button onClick={this.deletereview}>Törlés</button>
                       </div>)
                case 'reviewcreate':
                    return( <div className="deleteform"><h6>Vélemények hozzáadás</h6>
                    
                      <input onChange={this.ChangeValueInState} type="number" id="createUserCode" name="createUserCode2" placeholder="kód"/>
                      <input onChange={this.ChangeValueInState} type="text" id="createUserName" name="createUserName2" placeholder="név"/>
                      <input onChange={this.ChangeValueInState} type="number" id="toCreateStarts" name="toCreateStarts2" placeholder="csillagok száma"/>
                      <input onChange={this.ChangeValueInState} type="text" id="createUserDescription" name="createUserDescription2" placeholder="vélemény szöveg"/>
                         <button onClick={this.addreview}>Hozzáadás</button>
                    </div>)
            default:
                return    <h1>new</h1>
                
        }
    }
    setButtonState  =(value) =>{
        this.clearInputs()
        this.setState({option:value});

    }
    render(){
        return(
            <div className="mainsolver">
                <div className="Micronav">
                    <button onClick={(event)=>{
                        this.setButtonState(event.target.id)
                    }} id='new'>Új</button>
                    <button   onClick={(event)=>{
                       this.setButtonState(event.target.id)
                    }} id='delete'>Törlés</button>
                     <button   onClick={(event)=>{
                      this.setButtonState(event.target.id)
                    }} id='reviewdelete'>Vélemények törlés</button>
                    <button   onClick={(event)=>{
                      this.setButtonState(event.target.id)
                    }} id='reviewcreate'>Vélemények hozzáadás</button>
                </div>
                    <this.ResolveState/>
            </div>
        )
    }
}
export default AdminuserMG;


