import React from 'react';
import CountUp, {startAnimation} from 'react-countup';
import './numberblock.css';



const numberComponent = (input) =>{
    return input.records.map((inputs,i)=>{
        return  (
            <div className="u-align-center u-container-style u-layout-cell u-size-20 u-layout-cell-2">
                <div className="u-container-layout u-valign-middle u-container-layout-2">
                <h1 className="u-text u-title u-text-3" data-animation-name="counter" data-animation-event="scroll" data-animation-duration="3000">
                        <CountUp start={0} end={inputs.value} delay={0} duration={7.5}>
                        {({ countUpRef }) => (
                            <div>
                            <span ref={countUpRef} />
                            </div>
                        )}
                        </CountUp>
                     </h1>
              <p className="u-text u-text-4">{inputs.name}</p>
                </div>
            </div>
            )
        })
    }
    class Numberblock extends React.Component {
        
        
              
              
    constructor(props) {
        super(props);
        this.state = props;
       
       }
       
    render(){
        return(
            <section className="skrollable skrollable-between u-align-left u-clearfix u-image u-shading u-section-3" src="" data-image-width="800" data-image-height="533" id="sec-cc44">
        <div className="u-clearfix u-sheet u-sheet-1 main">
            <div className="u-clearfix u-layout-wrap u-layout-wrap-1">
            <div className="u-layout">
                <div className="u-layout-row">
                    {numberComponent(this.props.imput)}                           
                </div>
                
            </div>
            </div>
        </div>
        </section>
      )
    };
}

export default Numberblock;