import React from 'react';
import Navbar from '../containers/navbar/Navbar';
import Jumbotron from '../components/jumbotron/Jumbotron';
import Userdisplay from '../containers/userdisplay/UserDisplay';
import Numberblock from '../components/numberblock/Numberblock';
import Socialblock from  '../components/socialblock/Socialblock'
import Contactblock from '../components/contact/Contact'
import Adminlogin from '../components/adminlogin/adminlogin';
import Reviewblock from '../components/reviewblock/reviewblock';
import '../App.css';
const numberdata={
  records: [
    {
      key:3,
      name: "Jósok",
      value: 4
    },{key:4,
      name: "Órányi jóslás",
      value: 1000
    },{
      key:5,
      name: "Embercentrikus jósok",
      value: 1
    }
  ]
}
const cancount = false;
function App() {
  return (
    <div className="App">
      <Navbar></Navbar>
      <Jumbotron></Jumbotron>
     <Userdisplay></Userdisplay>
      <Reviewblock></Reviewblock>
     <Numberblock imput={numberdata} cancount={cancount}></Numberblock>

      <Contactblock></Contactblock>
      <Adminlogin></Adminlogin>
    </div>
  );
}

export default App;
