import React from 'react';
import './Contact.css'
import axios from 'axios';

var component  = {
  email: '',
  name: '',
  message: '',
  review: ''
};

const HandleSubmit = (event) =>{
  axios.post('https://192.168.1.69:8080/email',{
    name: component.name,
    email: component.email,
    message: component.message,
    review: component.review
  })
  event.preventDefault();
  
}
const HandleChange = (event) =>{
  
  switch(event.target.id){
    case "name":
      component.name = event.target.value;
      break;
    case "email":
      component.email = event.target.value;
        break;
    case "message":
      component.message = event.target.value;
          break;
    case "review":
          component.review = event.target.checked;
            break;
    default:
            break;
  }
}


var Contactblock = () => {
    
  
    return(
        <section className="u-clearfix u-expanded-width-xl u-section-5" id="kapcsolat">
  <div className="u-clearfix u-sheet u-sheet-1">
    <div className="u-clearfix u-expanded-width u-gutter-0 u-layout-wrap u-layout-wrap-1">
      <div className="u-gutter-0 u-layout">
        <div className="u-layout-row">
          <div className="u-size-27">
            <div className="u-layout-col">
              <div className="u-container-style u-layout-cell u-left-cell u-size-30 u-layout-cell-1">
                <div className="padded u-container-layout u-valign-middle u-container-layout-1">
                  <h2 className="u-text u-text-grey-70 u-text-1">Angyalvonal</h2>
                </div>
              </div>
              <div className="u-align-left u-container-style u-layout-cell u-left-cell u-size-30 u-layout-cell-2">
                <div className="u-container-layout u-container-layout-2">
                  <h2 className="u-text u-text-grey-70 u-text-3">Kövess minket</h2>
                  <div className="u-social-icons u-spacing-20 u-text-grey-80 u-social-icons-1" style={{height:"84px"}}>
                    <a className="u-social-url" href="https://facebook.com">
                      <span className="u-icon u-icon-circle u-social-facebook u-social-type-fill u-icon-1">
                        <svg className="u-svg-link" preserveAspectRatio="xMidYMin slice" viewBox="0 0 112 112" style={{}}><use xmlnsXlink="http://www.w3.org/1999/xlink" xlinkHref="#svg-44d7" /></svg>
                        <svg x="0px" y="0px" viewBox="0 0 112 112" id="svg-44d7" className="u-svg-content"><path style={{color:"#760d83"}}d="M56.1,0C25.1,0,0,25.1,0,56.1c0,31,25.1,56.1,56.1,56.1c31,0,56.1-25.1,56.1-56.1C112.2,25.1,87.1,0,56.1,0z M71.6,34.3h-8.2c-1.3,0-3.2,0.7-3.2,3.5v7.6h11.3l-1.3,12.9h-10V95H45V58.3h-7.2V45.4H45v-8.3c0-6,2.8-15.3,15.3-15.3l11.2,0V34.3z " /></svg>
                      </span>
                    </a>
                  
                  </div>
                  <p className="u-text u-text-4">©2020 Minden jog fenntartva</p>
                </div>
              </div>
            </div>
          </div>
          <div className="u-size-33">
            <div className="u-layout-row">
              <div className="u-container-style u-layout-cell u-right-cell u-size-60 u-layout-cell-3">
                <div className="u-container-layout u-valign-middle u-container-layout-3">
                  <h2 className="u-text u-text-grey-70 u-text-5">Kapcsolat</h2>
                  <div className="u-expanded-width u-form u-form-1">
                    <form className="u-block-3fe9-21 u-clearfix u-form-spacing-25 u-form-vertical u-inner-form">{/* hidden inputs for siteId and pageId */}
                      <div className="u-form-group u-form-name">
                        <label htmlFor="name-e4cc" className=" u-form-control-hidden u-label">Name</label>
                        <input onChange={HandleChange} type="text" placeholder="Az ön neve" id="name" name="name" className="inputr u-border-no-bottom u-border-no-left u-border-no-right u-border-no-top u-grey-5 u-input u-input-rectangle" required />
                      </div>{/* always visible */}
                      <div className="u-form-email u-form-group">
                        <label htmlFor="email-e4cc" className=" u-form-control-hidden u-label">Email</label>
                        <input onChange={HandleChange} type="email" id="email" name="email" className="inputr u-border-no-bottom u-border-no-left u-border-no-right u-border-no-top u-grey-5 u-input u-input-rectangle" required placeholder="Az ön E-mail címe" />
                      </div>
                      <div className="u-form-group u-form-message">
                        <label htmlFor="message-e4cc" className="u-form-control-hidden u-label">Message</label>
                        <textarea onChange={HandleChange} placeholder="Az üzenete" rows={4} cols={50} name="message" id="message" className="inputr u-border-no-bottom u-border-no-left u-border-no-right u-border-no-top u-grey-5 u-input u-input-rectangle" required defaultValue={""} />
                      </div>
                      <div className="u-form-group u-form-submit">
                        <div className="reviewbutton"><input  onChange={HandleChange} type="checkbox" value="review" id="review"></input><p> Véleményt irok</p></div>
                        <input type="submit" onClick={HandleSubmit} defaultValue="submit" name="Elküldés" value="Elküldés" className="u-btn u-btn-submit u-button-style" style={{width:"100%"}}/>
                      </div>
                      <div className="u-form-send-message u-form-send-success"> Thank you! Your message has been sent. </div>
                      <div className="u-form-send-error u-form-send-message"> Unable to send your message. Please fix errors then try again. </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>

    )
}

export default Contactblock;