import React from 'react';
import './App.css';

import {Switch,Route,BrowserRouter as Router} from 'react-router-dom';


import Home from "./page/home";
import Login from "./page/login";
import User from "./page/user";


function App() {
  return (
    <Router>
      <Switch>
      
          <Router path="/user">
            <User/>
          </Router>
          <Router path="/login">
            <Login/>
          </Router>
          <Route path="/home">
            <Home />
          </Route>
          <Route path="">
            <Home />
          </Route>
        </Switch>
    </Router>
  );
}

export default App;
