import React from 'react';
import Usercard from '../../components/usercard/Usercard';
import socket from '../../helpers/sockets';



  socket.on('event', function(data){
    component.setState({users:data});
    
  });


var value = 0;
var component = {
  setState: function (){}
};
class Userdisplay extends React.Component{

    constructor(props){
      super(props);
      component = this;
      
    }
    
   
    ResolveUsers = () => {
      if(this.state != null){
        return this.state.users.map((udata,i) =>{
          return <Usercard udata={udata}/>
        })

      }else
      {
        return <h1>Töltés...</h1>
      }
    }
    render(){
        return(
    
            <section className="u-align-center u-clearfix u-section-2" id="user">
             
            <div className="u-clearfix u-sheet u-valign-bottom u-sheet-1" style={{minWidth:'75%', paddingBottom:"50px"}}>
              <h1 className="u-text u-text-default u-title u-text-1">Jósaink</h1>
              <div className="u-border-3 u-border-grey-dark-1 u-line u-line-horizontal u-line-1" style={{marginBottom:'25px'}}></div>
              <div className="u-clearfix u-gutter-28 u-layout-wrap u-layout-wrap-1"style={{minWidth:'100%'}}>
                <div className="u-layout">
                  <div className="u-layout-col">
                    <div style={{display: 'flex',flexWrap : 'wrap',justifyContent: 'center', minHeight: '37rem',minWidth: '100%'}}>
                     <this.ResolveUsers/>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        )
    }
}
export default Userdisplay;